/* Header */ 

* {
    --padding-1: .5rem;
    --padding-2: .8rem;
    --padding-3: 1.2rem;
    --padding-4: 1.6rem;

    --font-xxs: .5em;
    --font-xs: .8em;
    --font-s: .9rem;
    --font-m: 1.2em;
    --font-l: 1.5em;
    --font-xl: 2em;

    --indent-1: 10%;
}

/* General */
.section_title {
    padding-top: var(--padding-4);
    padding-bottom: var(--padding-4);
    padding-left: var(--indent-1);
    font-size: var(--font-xl);
}
@media screen and (max-width: 999px) {
    .section_title {
        width: 100%;
        text-align: center;
        padding-left: 0px;
    }
}

.header {
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    box-sizing: border-box;
    background-color: var(--cream);
    display: flex;
    gap: var(--padding-3);
    flex-direction: row-reverse;
    padding: var(--padding-2);
}

.header a {
    text-decoration: none;
    color: var(--green_0);
}


/* Section 1 */ 
.landing {
    min-height: 100vh;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: var(--indent-1);
    background: rgb(7,34,29);
    background: linear-gradient(318deg, rgba(7,34,29,1) 0%, rgba(22,56,50,1) 100%);
}

.landing_main_content {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: var(--padding-2);
}

.landing p:nth-child(1) { font-size: var(--font-m);}
.landing p:nth-child(2) {font-size: 4.5em;}
.landing p:nth-child(3) {font-size: var(--font-l);}
.landing p:nth-child(4) {font-size: var(--font-m); }

.landing_links {
    margin-top: var(--padding-4);
    display: flex;
    flex-direction: row;
    gap: var(--padding-3);
}

.landing_links > a > img {
    width: var(--font-xl);
}

@media screen and (max-width: 999px) {
    .landing {
        padding-left: 0;
        align-items: center;
        text-align: center;
    }
    .landing_main_content {
        width: 95%;
    }
    .landing_links {
        justify-content: center;
    }
}

/* Section 2 */

.projects {
    height: fit-content;
}

.project_section {
    margin-top: var(--padding-4);
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.projects_list {
    width: 60%;
    max-width: 100rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
}


.project_container {
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
}
.project_container.right {
    align-self: end;
}

.project_info_container {
    width: 100%;
    max-width: 45rem;
}
.project_info_container.right {
    margin-left: auto;
}

.project_img_and_desc {
    display: flex;
    flex-direction: column;
    max-width: 45rem;
    padding: var(--padding-3);
}

.project_image {
    width: 35rem;
    height: 18rem;
    object-fit: cover;
    border-radius: 3rem;
    box-shadow: 0px 0px 3px var(--green_0);
}
.project_image.right {
    align-self: end;
}

.project_description {
    position: relative;
    top: -4rem;
    max-width: 35rem;
    background-color: var(--green_5);
    padding: var(--padding-2);
    border-radius: var(--padding-4);
    box-shadow: 0px 2px 10px var(--green_0);
}
.project_description.left {
    align-self: end;
}

.project_description_blurb {
    font-size: var(--font-s);
}

.project_title {
    font-size: var(--font-l);
    padding: var(--padding-1) 0px;
}

.project_technologies {
    display: flex;
    flex-direction: row;
    gap: var(--padding-3);
    padding: var(--padding-2) 0px;
}

.project_technologies > p {
    font-size: var(--font-xs);
    padding: var(--padding-1);
    background-color: #bce0c2;
    border-radius: calc(var(--padding-1) * 2.5);
}

.project_links > a {
    text-decoration: underline;
    color: var(--green_0);
    font-size: var(--font-xs);
    padding: var(--padding-1);
}

.project_border {
    width: 10px;
    height: 100%;
    background-color: black;
}

.project_next_line {
    position: absolute;
    bottom: -8rem;
    z-index: -2;
    width: 50%;
    height: 40%;
    border: 3px solid var(--green_4);
    border-radius: 2rem;
    border-top: none;
}
.project_next_line.left {
    left: 20%;
    border-right: none;
}

.project_next_line.right {
    right: 20%;
    border-left: none;
}

.project_next_line.last {
    display: none;
}

@media screen and (max-width: 1299px) {
    .projects_list {
        width: 95%;
        justify-content: center;
    }
}

@media screen and (max-width: 999px) {
    .project_container {
        justify-content: center;
    }
    .project_info_container.right {
        margin-left: initial;
    }
    .project_img_and_desc {
        align-items: center;
        padding: 0;
    }
    .project_description.left, .project_description{
        align-self: center;
        max-width: calc(35rem - var(--padding-2) * 2);
    }
    .project_next_line { display: none; }
    .project_image {
        max-width: 35rem;
        width: 99%;
        height: 65%;
    }
    .project_image.right {
        align-self: center;
    }
}


/* About */
.about {
    background-color: var(--green_2);
}

.who_i_am_container > h1, .skills_container > h1 {
    font-size: var(--font-xl);
    color: var(--green_5);
    font-weight: normal;
}

.about_container {
    display: flex;
    flex-direction: row;
    padding-bottom: 4rem;
    padding-top: 2rem;
    justify-content: center;
    margin-left: calc(var(--indent-1) / 2);
    margin-right: calc(var(--indent-1) / 2);
}

.who_i_am_container {
    width: 50%;
    max-width: 600px;
}
.who_i_am_container > p {
    width: 90%;
    padding-top: var(--padding-1);
    color: var(--green_4);
}

.skills_container {
    max-width: 600px;
    margin-right: 3rem;
}

.skills_list {
    display: flex;
    flex-direction: column;
    gap: var(--padding-3);
}

.skill_section {
    margin-left: var(--padding-3);
}

.skill_section_title {
    font-size: var(--font-l);
    font-weight: normal;
    color: var(--green_4);
}

.skills {
    border-left: 2px solid var(--green_5);
    margin-top: var(--padding-2);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: var(--padding-3);
    padding-left: var(--padding-2);
    margin-left: var(--padding-2);
}

.skill {
    background-color: var(--green_4);
    border-radius: var(--padding-4);
}
.skill > p {
    text-align: center;
    min-width: var(--padding-3);
    font-size: var(--font-xs);
    padding: var(--padding-1);
}

@media screen and (max-width: 999px) {
    .about_container {
        flex-direction: column;
        align-items: center;
        margin-left: 2%;
        margin-right: 2%;
        gap: 4rem;
    }
    .who_i_am_container {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        gap: var(--padding-4);
        width: 95%;
    }
    .skills_container {
        margin: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: var(--padding-4);
    }
    .skill_section {
        margin: 0;
        text-align: center;
    }
    .skill_section h1 {
        margin-bottom: var(--padding-3);
    }
    .skills {
        border: none;
        margin: 0;
        padding: 0;
        justify-content: center;
    }
    .skill_section:not(:last-child) {
        padding-bottom: var(--padding-4);
        border-bottom: 1px solid var(--cream);
    }
}

/* Contact */
.contact-blurb {
    margin-left: var(--indent-1);
    margin-bottom: var(--padding-4);
}

.contact-blurb p {
    font-size: var(--font-m);
    margin-bottom: var(--padding-3);
}

.contact-blurb a {
    color: var(--green_highlight);
}

@media screen and (max-width: 650px) {
    .contact-blurb {
        font-size: var(--font-s);
        width: 100%;
        margin: 0;
        padding: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .contact-blurb p {
        width: 85%;
        text-align: center;
    }
}

.footer {
    background-color: var(--green_2);
    min-height: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--cream);
}
